.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ListItem {
  height: 50px;
}

.DeleteButton {
  max-width: 30px;
  width: auto;
  height: auto;
  top: 50%;
  vertical-align: middle;
}

.DeleteButton:hover {
  max-width: 35px;
  vertical-align: middle;
}

.DeleteButton.Invisible {
  visibility: hidden;
}

ul.no-bullets {
  list-style-type: none;
  /* Remove bullets */
  padding: 0;
  /* Remove padding */
  margin: 0;
  /* Remove margins */
}

.ListItemInput {
  width: 500px;
  height: 50px;
  margin-top: 50px;
  font-size: larger;
}

.SubmitButton {
  width: 100px;
  height: 40px;
  margin-top: 30px;
  font-size: large;
}